.formWrapper {
    display: flex;
    flex-direction: column;
    border: 1px solid #e1e1e1;
    width: auto;
    max-width: 50%;
    box-sizing: border-box;
    padding: 1rem;
    margin: 1rem;
    border-radius: 0.25rem;
    box-shadow: 0 0 0.5rem #3333;
}


.formWrapper h1 {
    font-size: 1.5rem;
    padding: 0;
    line-height: 0.75;
    margin: 0.25rem;

}

.form {
    display: flex;
    flex-direction: column;
}

.form input {
    box-sizing: border-box;
    padding: 0.25rem;
    margin: 0.25rem 0;
    border: 1px solid #e1e1e1;
    border-radius: 0.25em;
    transition: 0.3s;
}

.form input:focus {
    border-color: #203864;
   outline: none;
}

.form .rememberWrapper {
    display: flex;
    align-items: center;
}

.form .rememberWrapper input{
    width: 14px;
    height: 14px;
    box-sizing: border-box;
    margin: 5px;
    margin-top: 8px;
}

.form .loginButton {
    background: rebeccapurple;
    border: 0;
    box-sizing: border-box;
    padding: 0.5rem 1rem;
    color: #fff;
    border-radius: 00.25rem;
    cursor: pointer;
    transition: 0.3s;
    font-size: 1rem;
}

.form input:hover, .form  button:hover {
    box-shadow: 0 0 0.25rem #3333;
}

.error_wrap {
    color: #fff;
    margin: 5px 0;
    padding: 5px;
    border: 1px solid #cc7722;
    background: #ee7744;
    border-radius: 3px;
}