

.app-wrapper {
  display: grid;
  grid-template-areas: 
  "header header"
  "nav content"
  "footer footer";
  grid-template-rows: 50px 1fr 50px;
  grid-template-columns: 2fr 10fr;
  /* grid-gap: 10px; */
  min-height: 100vh;
  max-width: 960px;
  margin: 0 auto;
}

.app-wrapper-content {
  grid-area: content;
}

.posts-wrapper {
  margin: 0.5em;
}

.post {
  padding: 0.5em;
  border-radius: 0.5em;
  border: 1px solid #e1e1e1;
}
.post .user-name {
  font-size: 1.2em;
}

.new_post {
  display: flex;
  flex-direction: column;

}

.new_post textarea {
  border: 1px solid #e1e1e1;
  border-radius: 0.25em;
  margin-bottom: 0.5em;
  padding: 0.5em;
}

.new_post button {
  background-color: #333;
  color: #ffff;
  border: 0;
  border-radius: 3px;
  padding: 0.5em;
  font-size: 14px;
  cursor: pointer;
}