.nav {
    grid-area: nav;
    background-color: #fff;
    border-right: 1px solid #e1e1e1;
    position: sticky;
    top: 0;
    display: flex;
    flex-direction: column;
  }
  
  .nav a {
    border-bottom: 1px solid #ccc;
    padding: 10px 15px;
    color: #333;
    text-decoration: none;
  }

  .nav a.active {
    font-weight: bold;
  }
  .link {
    color: #777;
  }

  .friend_block {
    display: flex;
  }

  .friend_block div {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    margin: 0.2em;
  }

  .friend_icon {
    width: 2em;
    height: 2em;
    background: #333;
    border-radius: 50%;
  }
  .friend_label {
    font-size: 0.7em;
  }